<template>
	<div class="container">
		<!-- <button type="button" class="btn" @click="showModal">
			Open Modal!
		</button> -->

		<Modal
			v-show="isModalVisible"
			@close="closeModal"
			@yes="clear"
			:isConfirm="true"
		>
			<template v-slot:header>
				Уверены ли вы?
			</template>
		</Modal>

		<Modal v-show="isModalFarmVisible" @close="closeModal" :isConfirm="false">
			<template v-slot:header>
				Не хватает свободных ферм!
			</template>
		</Modal>

		<Modal v-show="isModalMoneyVisible" @close="closeModal" :isConfirm="false">
			<template v-slot:header>
				Не хватает денег в казне!
			</template>
		</Modal>

		<Modal v-show="isModalMramorVisible" @close="closeModal" :isConfirm="false">
			<template v-slot:header>
				Не достаточно мрамора!
			</template>
		</Modal>

		<MainCard
			:moneyTree="moneyTree"
			:moneyOne="moneyOne"
			:moneyTwo="moneyTwo"
			:freeFarm="freeFarmComputed"
			:powerNation="powerNation"
			:moneyShareTotal="moneyShareTotal"
			@updateMoney="updateMoneyShareTotal"
		/>

		<div class="banner-mini">
			<Card
				v-model="cardFarm"
				:image="'field.png'"
				:text="'Фермы'"
				:color="'#b57e37'"
			></Card>
			<Card
				v-model="cardStada"
				:image="'horse.png'"
				:text="'Стада'"
				:color="'#8D6857'"
			></Card>
			<Card
				v-model="cardGold"
				:image="'gold.png'"
				:text="'Золото'"
				:color="'#BE953B'"
			></Card>
			<Card
				v-model="cardSamozvet"
				:image="'stone.png'"
				:text="'Самоцветы'"
				:color="'#C75A87'"
			></Card>
			<Card
				v-model="cardSalt"
				:image="'salt.png'"
				:text="'Соль'"
				:color="'#696767'"
			></Card>
			<Card
				v-model="cardMramor"
				:image="'marble.png'"
				:text="'Мрамор'"
				:color="'#8E8C8C'"
			></Card>
			<Card
				v-model="cardRuda"
				:image="'ore.png'"
				:text="'Руда'"
				:color="'#874F4F'"
			></Card>
			<Card
				v-model="cardSera"
				:image="'sulfur.png'"
				:text="'Сера'"
				:color="'#888102'"
			></Card>
			<Card
				v-model="cardUgol"
				:image="'coal.png'"
				:text="'Уголь'"
				:color="'#424242'"
			></Card>
			<Card
				v-model="cardEssenzia"
				:image="'essence.png'"
				:text="'Эссенция'"
				:color="'#549445'"
			></Card>
			<Card
				v-model="cardObsidian"
				:image="'obsidian.png'"
				:text="'Обсидиан'"
				:color="'#5E5F5E'"
			></Card>
			<Card
				v-model="cardAlmazi"
				:image="'diamond.png'"
				:text="'Алмазы'"
				:color="'#358894'"
			></Card>
			<Card
				v-model="cardGlina"
				:image="'clay.png'"
				:text="'Глина'"
				:color="'#81574A'"
			></Card>
			<Card
				v-model="cardRtut"
				:image="'mercury.png'"
				:text="'Ртуть'"
				:color="'#5A5A5A'"
			></Card>

			<div class="gray-banner">
				<div class="font-name gray-banner-centering">Армия 1</div>
				<div class="line font-name">
					<div
						v-for="(oneBtn, index) of armyOneArr"
						class="white-box"
						:class="oneBtn ? 'button-active' : ''"
						@click="clickArmyOne(index + 1)"
					>
						{{ index + 1 }}
					</div>
				</div>
			</div>
			<div class="gray-banner">
				<div class="font-name gray-banner-centering">Армия 2</div>
				<div class="line font-name">
					<div
						v-for="(oneBtn, index) of armyTwoArr"
						class="white-box"
						:class="oneBtn ? 'button-active' : ''"
						@click="clickArmyTwo(index + 1)"
					>
						{{ index + 1 }}
					</div>
				</div>
			</div>
			<div class="gray-banner">
				<div class="font-name gray-banner-centering">Армия 3</div>
				<div class="line font-name">
					<div
						v-for="(oneBtn, index) of armyThreeArr"
						class="white-box"
						:class="oneBtn ? 'button-active' : ''"
						@click="clickArmyThree(index + 1)"
					>
						{{ index + 1 }}
					</div>
				</div>
			</div>
			<div class="gray-banner-box gray-banner">
				<div class="gray-banner gray-banner-city">
					<div class="font-name gray-banner-centering">Город 1</div>
					<div class="line font-name">
						<div class="white-box" @click="decrease">-</div>
						<div class="brown-box">{{ counterCity }}</div>
						<div class="white-box" @click="add">+</div>
					</div>
				</div>
				<div class="gray-banner gray-banner-city">
					<div class="font-name gray-banner-centering">Город 2</div>
					<div class="line font-name">
						<div class="white-box" @click="decreaseTwo">-</div>
						<div class="brown-box">{{ counterCityTwo }}</div>
						<div class="white-box" @click="addTwo">+</div>
					</div>
				</div>
			</div>
			<div>
				<div class="gray-banner">
					<div class="font-name gray-banner-centering">Город 3</div>
					<div class="line-close font-name">
						<div class="white-box" @click="decreaseThree">-</div>
						<div class="brown-box">{{ counterCityThree }}</div>
						<div class="white-box" @click="addThree">+</div>
					</div>
				</div>
			</div>
			<div class="white-banner">
				<div class="font-name-dark white-box-big" @click="showModal">
					Cбросить
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Card from '@/components/Card.vue'
import MainCard from '@/components/MainCard.vue'
import Modal from '@/components/Modal.vue'

export default {
	name: 'Home',
	components: {
		Card,
		MainCard,
		Modal,
	},
	data() {
		return {
			counterCity: 1,
			counterCityTwo: 0,
			counterCityThree: 0,
			isModalVisible: false,
			isModalFarmVisible: false,
			isModalMoneyVisible: false,
			isModalMramorVisible: false,
			armyOneArr: [false, false, false, false, false, false, false, false],
			armyTwoArr: [false, false, false, false, false, false, false, false],
			armyThreeArr: [false, false, false, false, false, false, false, false],
			freeFarm: 0,
			moneyOne: 0,
			moneyTwo: 0,
			powerNation: 0,
			cardFarm: 0,
			cardStada: 0,
			cardGold: 0,
			cardSamozvet: 0,
			cardSalt: 0,
			cardMramor: 0,
			cardRuda: 0,
			cardSera: 0,
			cardUgol: 0,
			cardEssenzia: 0,
			cardObsidian: 0,
			cardAlmazi: 0,
			cardGlina: 0,
			cardRtut: 0,
			moneyShareTotal: 0,
		}
	},
	mounted() {
		this.defaultValue()
		this.getLocalStorage()
	},
	watch: {
		cardFarm() {
			console.log(this.cardFarm)
			this.freeFarm = this.cardFarm

			this.calcPowerNation()
		},
		cardSalt() {
			console.log(this.cardSalt)
			this.freeFarm = this.cardFarm + this.cardSalt

			this.calcPowerNation()
		},
		cardGold() {
			this.moneyOne = this.cardGold + this.cardSamozvet * 2
			console.log(this.cardGold)

			this.calcPowerNation()
		},
		cardSamozvet() {
			this.moneyOne = this.cardGold + this.cardSamozvet * 2
			console.log(this.cardSamozvet)

			this.calcPowerNation()
		},
		cardMramor() {
			if (this.calcNecessaryMramor() > this.cardMramor) {
				this.isModalMramorVisible = true
			}

			this.calcPowerNation()
		},

		cardStada() {
			this.calcPowerNation()
		},

		cardRuda() {
			this.calcPowerNation()
		},

		cardSera() {
			this.calcPowerNation()
		},

		cardUgol() {
			this.calcPowerNation()
		},

		cardEssenzia() {
			this.calcPowerNation()
		},

		cardObsidian() {
			this.calcPowerNation()
		},

		cardAlmazi() {
			this.calcPowerNation()
		},

		cardGlina() {
			this.calcPowerNation()
		},

		cardRtut() {
			this.calcPowerNation()
		},

		counterCity(newVal, oldVal) {
			if (this.counterCity > 8) {
				this.counterCity = 8
			}
			console.log(this.counterCity)
			this.moneyTwo = this.calcMoneyTwo()

			if (newVal > oldVal) {
				this.moneyShareTotal = this.moneyShareTotal - this.calcUsedShareMoney(1)
				if (this.moneyShareTotal < 0) {
					this.moneyShareTotal = 0
					this.isModalMoneyVisible = true
				}
			}

			if (this.calcNecessaryMramor() > this.cardMramor) {
				this.isModalMramorVisible = true
			}
		},
		counterCityTwo(newVal, oldVal) {
			if (this.counterCityTwo > 8) {
				this.counterCityTwo = 8
			}
			console.log(this.counterCityTwo)
			this.moneyTwo = this.calcMoneyTwo()

			if (newVal > oldVal) {
				this.moneyShareTotal = this.moneyShareTotal - this.calcUsedShareMoney(2)
				if (this.moneyShareTotal < 0) {
					this.moneyShareTotal = 0
					this.isModalMoneyVisible = true
				}
			}

			if (this.calcNecessaryMramor() > this.cardMramor) {
				this.isModalMramorVisible = true
			}
		},
		counterCityThree(newVal, oldVal) {
			if (this.counterCityThree > 8) {
				this.counterCityThree = 8
			}
			console.log(this.counterCityThree)
			this.moneyTwo = this.calcMoneyTwo()

			if (newVal > oldVal) {
				this.moneyShareTotal = this.moneyShareTotal - this.calcUsedShareMoney(3)
				if (this.moneyShareTotal < 0) {
					this.moneyShareTotal = 0
					this.isModalMoneyVisible = true
				}
			}

			if (this.calcNecessaryMramor() > this.cardMramor) {
				this.isModalMramorVisible = true
			}
		},
		armyOneArr() {
			console.log(this.armyOneArr)
		},
		armyTwoArr() {
			console.log(this.armyTwoArr)
		},
		armyThreeArr() {
			console.log(this.armyThreeArr)
		},
	},
	computed: {
		moneyTree() {
			return this.moneyOne + this.moneyTwo
		},
		freeFarmComputed() {
			if (this.cardSalt >= 3) {
				let localFreeFarm = this.freeFarm * 2

				localFreeFarm = localFreeFarm - this.howMuchFreeFarmUsedByCity()
				localFreeFarm = localFreeFarm - this.howMuchFreeFarmUsedByArmy()

				if (localFreeFarm < 0) {
					this.isModalFarmVisible = true
					localFreeFarm = 0
				}

				return localFreeFarm
			} else {
				let localFreeFarm = this.freeFarm

				localFreeFarm = localFreeFarm - this.howMuchFreeFarmUsedByCity()
				localFreeFarm = localFreeFarm - this.howMuchFreeFarmUsedByArmy()

				if (localFreeFarm < 0) {
					this.isModalFarmVisible = true
					localFreeFarm = 0
				}

				return localFreeFarm
			}
		},
	},
	methods: {
		saveLocalStorage() {
			let data = {
				counterCity: this.counterCity,
				counterCityTwo: this.counterCityTwo,
				counterCityThree: this.counterCityThree,
				isModalVisible: this.isModalVisible,
				isModalFarmVisible: this.isModalFarmVisible,
				isModalMoneyVisible: this.isModalMoneyVisible,
				isModalMramorVisible: this.isModalMramorVisible,
				armyOneArr: this.armyOneArr,
				armyTwoArr: this.armyTwoArr,
				armyThreeArr: this.armyThreeArr,
				freeFarm: this.freeFarm,
				moneyOne: this.moneyOne,
				moneyTwo: this.moneyTwo,
				powerNation: this.powerNation,
				cardFarm: this.cardFarm,
				cardStada: this.cardStada,
				cardGold: this.cardGold,
				cardSamozvet: this.cardSamozvet,
				cardSalt: this.cardSalt,
				cardMramor: this.cardMramor,
				cardRuda: this.cardRuda,
				cardSera: this.cardSera,
				cardUgol: this.cardUgol,
				cardEssenzia: this.cardEssenzia,
				cardObsidian: this.cardObsidian,
				cardAlmazi: this.cardAlmazi,
				cardGlina: this.cardGlina,
				cardRtut: this.cardRtut,
				moneyShareTotal: this.moneyShareTotal,
			}
			localStorage.setItem('data', JSON.stringify(data))
		},
		getLocalStorage() {
			let data = localStorage.getItem('data')
			if (data && data.length) {
				data = JSON.parse(data)

				this.counterCity = data.counterCity
				this.counterCityTwo = data.counterCityTwo
				this.counterCityThree = data.counterCityThree
				this.isModalVisible = data.isModalVisible
				this.isModalFarmVisible = data.isModalFarmVisible
				this.isModalMoneyVisible = data.isModalMoneyVisible
				this.isModalMramorVisible = data.isModalMramorVisible
				this.armyOneArr = data.armyOneArr
				this.armyTwoArr = data.armyTwoArr
				this.armyThreeArr = data.armyThreeArr
				this.freeFarm = data.freeFarm
				this.moneyOne = data.moneyOne
				this.moneyTwo = data.moneyTwo
				this.powerNation = data.powerNation
				this.cardFarm = data.cardFarm
				this.cardStada = data.cardStada
				this.cardGold = data.cardGold
				this.cardSamozvet = data.cardSamozvet
				this.cardSalt = data.cardSalt
				this.cardMramor = data.cardMramor
				this.cardRuda = data.cardRuda
				this.cardSera = data.cardSera
				this.cardUgol = data.cardUgol
				this.cardEssenzia = data.cardEssenzia
				this.cardObsidian = data.cardObsidian
				this.cardAlmazi = data.cardAlmazi
				this.cardGlina = data.cardGlina
				this.cardRtut = data.cardRtut
				this.moneyShareTotal = data.moneyShareTotal
			}
		},
		defaultValue() {
			this.moneyShareTotal = 35
			this.moneyTwo = 1
			this.counterCity = 1
		},
		calcPowerNation() {
			this.powerNation = 0
			this.powerNation += this.cardFarm
			this.powerNation += this.cardStada
			this.powerNation += this.cardGold
			this.powerNation += this.cardSamozvet
			this.powerNation += this.cardSalt
			this.powerNation += this.cardMramor
			this.powerNation += this.cardRuda
			this.powerNation += this.cardSera
			this.powerNation += this.cardUgol
			this.powerNation += this.cardEssenzia
			this.powerNation += this.cardObsidian
			this.powerNation += this.cardAlmazi
			this.powerNation += this.cardGlina
			this.powerNation += this.cardRtut

			this.powerNation += this.howMuchFreeFarmUsedByArmy()
			this.powerNation += this.counterCity
			this.powerNation += this.counterCityTwo
			this.powerNation += this.counterCityThree

			if (Math.floor(this.cardStada / 2) <= 3) {
				this.powerNation += Math.floor(this.cardStada / 2)
			} else {
				this.powerNation += 3
			}

			if (this.cardSalt >= 3) {
				this.powerNation += 3
			}

			if (this.cardRuda >= 3) {
				this.powerNation += 3
			}

			this.powerNation += Math.floor(this.moneyShareTotal / 10)

			this.saveLocalStorage()
		},
		calcNecessaryMramor() {
			let count = 0
			if (this.counterCity === 7) {
				count++
			}
			if (this.counterCity === 8) {
				count = count + 2
			}

			if (this.counterCityTwo === 7) {
				count++
			}
			if (this.counterCityTwo === 8) {
				count = count + 2
			}

			if (this.counterCityThree === 7) {
				count++
			}
			if (this.counterCityThree === 8) {
				count = count + 2
			}

			return count
		},
		updateMoneyShareTotal(data) {
			this.moneyShareTotal = data
			console.log(this.moneyShareTotal)

			this.calcPowerNation()
		},
		calcUsedShareMoney(cityNumber) {
			let count = 0

			switch (cityNumber) {
				case 1:
					if (this.counterCity > 1 && this.counterCity < 7) {
						count = count + 10
					} else if (this.counterCity > 6 && this.counterCity < 9) {
						count = count + 20
					}
					return count

				case 2:
					if (this.counterCityTwo > 1 && this.counterCityTwo < 7) {
						count = count + 10
					} else if (this.counterCityTwo > 6 && this.counterCityTwo < 9) {
						count = count + 20
					}
					return count

				case 3:
					if (this.counterCityThree > 1 && this.counterCityThree < 7) {
						count = count + 10
					} else if (this.counterCityThree > 6 && this.counterCityThree < 9) {
						count = count + 20
					}
					return count

				default:
					break
			}
		},
		calcMoneyTwo() {
			return this.counterCity + this.counterCityTwo + this.counterCityThree
		},
		howMuchFreeFarmUsedByArmy() {
			let totalCount = 0

			let count1 = this.armyOneArr.filter(a => a === true).length
			let count2 = this.armyTwoArr.filter(a => a === true).length
			let count3 = this.armyThreeArr.filter(a => a === true).length

			totalCount = count1 + count2 + count3

			return totalCount
		},
		howMuchFreeFarmUsedByCity() {
			let count = 0
			if (this.counterCity > 1) {
				count = count + (this.counterCity - 1)
			}
			if (this.counterCityTwo > 1) {
				count = count + (this.counterCityTwo - 1)
			}
			if (this.counterCityThree > 1) {
				count = count + (this.counterCityThree - 1)
			}
			return count
		},
		clickArmyOne(value) {
			this.armyOneArr[value - 1] = !this.armyOneArr[value - 1]

			this.armyOneArr.push(true)
			this.armyOneArr.splice(this.armyOneArr.length - 1)

			this.calcPowerNation()
		},
		clickArmyTwo(value) {
			this.armyTwoArr[value - 1] = !this.armyTwoArr[value - 1]

			this.armyTwoArr.push(true)
			this.armyTwoArr.splice(this.armyTwoArr.length - 1)

			this.calcPowerNation()
		},
		clickArmyThree(value) {
			this.armyThreeArr[value - 1] = !this.armyThreeArr[value - 1]

			this.armyThreeArr.push(true)
			this.armyThreeArr.splice(this.armyThreeArr.length - 1)

			this.calcPowerNation()
		},
		clear() {
			this.closeModal()

			this.counterCity = 0
			this.counterCityTwo = 0
			this.counterCityThree = 0
			this.isModalVisible = false
			this.isModalFarmVisible = false
			this.isModalMoneyVisible = false
			this.isModalMramorVisible = false
			this.armyOneArr = [false, false, false, false, false, false, false, false]
			this.armyTwoArr = [false, false, false, false, false, false, false, false]
			this.armyThreeArr = [
				false,
				false,
				false,
				false,
				false,
				false,
				false,
				false,
			]
			this.freeFarm = 0
			this.moneyOne = 0
			this.moneyTwo = 0
			this.powerNation = 0
			this.cardFarm = 0
			this.cardStada = 0
			this.cardGold = 0
			this.cardSamozvet = 0
			this.cardSalt = 0
			this.cardMramor = 0
			this.cardRuda = 0
			this.cardSera = 0
			this.cardUgol = 0
			this.cardEssenzia = 0
			this.cardObsidian = 0
			this.cardAlmazi = 0
			this.cardGlina = 0
			this.cardRtut = 0
			this.moneyShareTotal = 0

			this.defaultValue()
			this.saveLocalStorage()
		},
		decrease() {
			this.counterCity = this.counterCity - 1
			if (this.counterCity < 0) {
				this.counterCity = 0
			}

			this.calcPowerNation()
		},
		add() {
			this.counterCity = this.counterCity + 1

			this.calcPowerNation()
		},
		decreaseTwo() {
			this.counterCityTwo = this.counterCityTwo - 1
			if (this.counterCityTwo < 0) {
				this.counterCityTwo = 0
			}

			this.calcPowerNation()
		},
		addTwo() {
			this.counterCityTwo = this.counterCityTwo + 1

			this.calcPowerNation()
		},
		decreaseThree() {
			this.counterCityThree = this.counterCityThree - 1
			if (this.counterCityThree < 0) {
				this.counterCityThree = 0
			}

			this.calcPowerNation()
		},
		addThree() {
			this.counterCityThree = this.counterCityThree + 1

			this.calcPowerNation()
		},
		showModal() {
			this.isModalVisible = true
		},
		closeModal() {
			this.isModalVisible = false
			this.isModalFarmVisible = false
			this.isModalMoneyVisible = false
			this.isModalMramorVisible = false
		},
	},
}
</script>

<style>
.container {
	display: flex;
	background-color: #26322b;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
	position: relative;
}
.banner-mini {
	margin: 0;
	padding: 0;
	display: flex;
	position: relative;
	flex-direction: column;
	width: 100%;
	background-color: #000;
}
.line {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.font-name {
	color: #ffffff;
	text-transform: uppercase;
	/* font-size: 16px; */
	/* font-weight: 600; */
	align-items: center;
}
.bg-orange {
	background-color: #b57e37;
	height: 48px;
	align-items: center;
	display: flex;
